}
.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 0%);
  pointer-events: none;
  opacity: 0;
  padding: 2em;
  border: 1px solid gray;

  transition:
    transform 175ms ease-out,
    opacity 200ms ease;
}
.modal.show {
  opacity: 1;
  pointer-events: all;
  transform: translate(-50%, -50%);
}
